import React, { useState } from "react";
import { Typography, Col, Row, Divider, Avatar, Drawer } from "antd";
import Background from "../img/backgroundCar.jpg";
import SignInForm from "../components/Register/SignInForm";
import SignupForm from "../components/Register/SignupForm";
import { Redirect } from "react-router";
// import SignInForm from "./SignInForm";
// import SignupForm from "./SignupForm";

const { Link } = Typography;

const Login = () => {
  const [visible, setVisible] = useState(true);
  const [currentForm, setCurrentForm] = useState("signin");
  const showDrawer = () => {
    setVisible(true);
  };
  const onClose = () => {
    setVisible(false);
  };

  return (
    <>
      {window.localStorage.token && <Redirect to="/" />}
      <Drawer
        placement="right"
        closable={true}
        onClose={onClose}
        visible={visible}
        width={400}
      >
        <Row justify="center" align="bottom" style={{ marginTop: 50 }}>
          <Col>
            {currentForm === "signin" && <SignInForm />}
            {currentForm === "signup" && <SignupForm />}
            <Divider style={{ marginBottom: 0, paddingBottom: 5 }}></Divider>

            <Row justify="center" gutter={[0, 20]}>
              <Divider type="vertical"></Divider>
            </Row>
            <Row justify="center">
              <Link
                onClick={() => {
                  setCurrentForm(
                    currentForm === "signup" ? "signin" : "signup"
                  );
                }}
              >
                {currentForm === "signup"
                  ? "Already have account?"
                  : "Register Now!"}
              </Link>
            </Row>
          </Col>
        </Row>
        <Row style={{ position: "absolute", bottom: 0, right: 0 }}>
          <Col>
            <img src={Background} alt="" style={{ width: 100 }} />
          </Col>
        </Row>
      </Drawer>
    </>
  );
};
export default Login;
