import React, { useState } from "react";
import {
  Input,
  Form,
  Typography,
  Button,
  Col,
  Row,
  Alert,
  message,
} from "antd";
import { LoginOutlined, UserOutlined, LockOutlined } from "@ant-design/icons";
import { signIn } from "../../api/auth";

const { Title, Link, Text } = Typography;
const buttonStyle = {
  backgroundColor: "#FFEA00",
  borderColor: "#FFEA00",
};
const fontStyle = {
  fontFamily: "agrandir",
};
const SignInForm = () => {
  const [errorMsg, setErrorMsg] = useState("");
  const [loading, setLoading] = useState(false);
  const onFinish = async (values) => {
    try {
      setLoading(true);
      setErrorMsg("");
      const res = await signIn(values);
      if (res.data && res.data.token) {
        window.localStorage.setItem("token", res.data.token);
        window.location.reload();
        message.success("Registered Successfully!");
      }
      setLoading(false);
    } catch (error) {
      setLoading(false);
      console.log(error.response);
      if (
        error.response &&
        error.response.data &&
        error.response.data.message
      ) {
        setErrorMsg(
          error.response.data.message || "Something went wrong, try again."
        );
      }
    }
  };

  return (
    <Form onFinish={onFinish}>
      <Row justify="center">
        {errorMsg && (
          <Alert
            message="Forbidden"
            description={errorMsg}
            type="error"
            showIcon
            closable
          />
        )}
        <Title style={fontStyle}>Sign In</Title>
      </Row>
      <Row justify="center">
        <Form.Item
          name="email"
          rules={[{ required: true, message: "Please input your Email!" }]}
        >
          <Input
            prefix={<UserOutlined className="site-form-item-icon" />}
            placeholder="Username"
          />
        </Form.Item>
      </Row>
      <Row justify="center">
        <Form.Item
          style={{ marginBottom: 0 }}
          name="password"
          rules={[{ required: true, message: "Please input your Password!" }]}
        >
          <Input
            prefix={<LockOutlined className="site-form-item-icon" />}
            type="password"
            placeholder="Password"
          />
        </Form.Item>
      </Row>
      <Row gutter={[0, 0]} align="top" justify="end">
        <Col span={13} style={{ paddingTop: 0, marginBottom: 20 }}>
          <Link style={{ color: "black", fontStyle }}>
            <small>Forgot Password?</small>
          </Link>
        </Col>
      </Row>
      <Row justify="center">
        <Button
          type="primary"
          shape="round"
          htmlType="submit"
          loading={loading}
          style={buttonStyle}
          icon={<LoginOutlined style={{ color: "black" }} />}
          size="large"
        >
          <Text color="Black">Sign In</Text>
        </Button>
      </Row>
    </Form>
  );
};

export default SignInForm;
