import React, { useEffect, useState } from "react";
import {
  HeartOutlined,
  ContactsOutlined,
  MessageOutlined,
  HeartFilled,
} from "@ant-design/icons";
import { Link } from "react-router-dom";
import { fetchAllCars, markFav } from "../api/cars";
import FilterSection from "../components/Filter/FilterSection";
import { Card, Typography, Col, Row, Drawer, Divider, Carousel } from "antd";
import Carmeter from "../img/carmeter.svg";

import Background from "../img/backgroundCar.jpg";
import SignInForm from "../components/Register/SignInForm";
import SignupForm from "../components/Register/SignupForm";

const { Title } = Typography;

const Cars = () => {
  const imgStyle = {
    maxHeight: "160px", width: "100%", objectFit: "cover"
  }
  const [loading, setLoading] = useState();
  const [cars, setCars] = useState([]);
  const [visible, setVisible] = useState(false);
  const [currentForm, setCurrentForm] = useState("signin");

  const onClose = () => {
    setVisible(false);
  };

  const fetchCars = async () => {
    try {
      setLoading(true);
      const res = await fetchAllCars();
      if (res.data && res.data.cars) {
        setCars(res.data.cars);
      }
      setLoading(false);
    } catch (error) {
      setLoading(false);
    }
  };

  const mountEffect = () => {

    fetchCars();
  };
  useEffect(mountEffect, []);
  return (
    <>
      <Drawer
        placement="right"
        closable={true}
        onClose={onClose}
        visible={visible}
        width={400}
      >
        <Row justify="center" align="bottom" style={{ marginTop: 50 }}>
          <Col>
            {currentForm === "signin" && <SignInForm />}
            {currentForm === "signup" && <SignupForm />}
            <Divider style={{ marginBottom: 0, paddingBottom: 5 }}></Divider>

            <Row justify="center" gutter={[0, 20]}>
              <Divider type="vertical"></Divider>
            </Row>
            <Row justify="center">
              <Link
                onClick={() => {
                  setCurrentForm(
                    currentForm === "signup" ? "signin" : "signup"
                  );
                }}
              >
                {currentForm === "signup"
                  ? "Already have account?"
                  : "Register Now!"}
              </Link>
            </Row>
          </Col>
        </Row>
        <Row style={{ position: "absolute", bottom: 0, right: 0 }}>
          <Col>
            <img src={Background} alt="" style={{ width: 100 }} />
          </Col>
        </Row>
      </Drawer>
      <FilterSection />
      <Row
        gutter={12, 25}
        style={{
          margin: "5rem"
        }}
      >
        {cars.map((item, index) => {
          return (
            <Col key={index} span={6} style={{ marginBottom: "0.7rem" }}>

              <Card
                onClick={() => {
                  if (!window.localStorage.token) {
                    setVisible(true);
                  }
                }}
                style={{ padding: "0px" }}
                cover={
                  <Link to={window.localStorage.token && `/view-ad/${item._id}`}>
                    <Carousel autoplay>
                      {item.carImages &&
                        item.carImages.map((item) => {
                          console.log(item);
                          return (
                            <div align="center">
                              <img
                                style={imgStyle}
                                src={`https://car-bar.herokuapp.com/${item}`}
                                alt=""
                              />
                            </div>
                          );
                        })}
                    </Carousel>
                  </Link>
                }
                loading={loading}
                bodyStyle={{ padding: "10px 24px" }}
                actions={[
                  <ContactsOutlined
                    style={{ fontSize: "27px" }}
                    key="setting"
                  />,
                  <MessageOutlined style={{ fontSize: "27px" }} key="edit" />,
                  <>
                    {item.isFavourite ? (
                      <HeartFilled
                        onClick={async (event) => {
                          await markFav({
                            carId: item._id,
                            status: false,
                          });
                          fetchCars()
                        }}
                        style={{ color: "red", fontSize: "27px" }}
                        key="ellipsis"
                      />
                    ) : (
                      <HeartOutlined
                        onClick={async (event) => {
                          await markFav({
                            carId: item._id,
                            status: true,
                          });
                          fetchCars()
                        }}
                        style={{ color: "red", fontSize: "27px" }}
                        key="ellipsis"
                      />
                    )}
                  </>,
                ]}
              >
                <Title
                  ellipsis={true}

                  level={3}
                  style={{
                    display: "flex",
                    justifyContent: "space-between",
                    color: "#001529",
                    margin: "0px",
                    marginTop: "10px",
                  }}
                >
                  {item.make} {item.model} {item.modelYear}
                </Title>
                <Title ellipsis={true} level={3} style={{ color: "#006004", margin: "0px" }}>
                  {" "}
                    PKR {item.price}
                </Title>
                <Title level={4} style={{ color: "#8F8F8F", margin: "0px" }}>
                  {item.city}
                </Title>
                <Row align="bottom" gutter={5}>
                  <Col>
                    <Title
                      level={4}
                      style={{ color: "#8F8F8F", margin: "0px" }}
                    >
                      {item.milage}
                    </Title>
                  </Col>
                  <Col>
                    <img src={Carmeter} alt=""></img>
                  </Col>
                </Row>
              </Card>

            </Col>
          );
        })}
      </Row>
    </>
  );
};

export default Cars;
