import React, { useState, useEffect } from "react";
import OverPack from "rc-scroll-anim/lib/ScrollOverPack";
import QueueAnim from "rc-queue-anim";
import { Row, Col, Card, Typography, Divider, Carousel } from "antd";
import { markFav } from "../../api/cars";
import { getChildrenToRender } from "../../utils";
import {
  ContactsOutlined,
  HeartOutlined,
  MessageOutlined,
  HeartFilled
} from "@ant-design/icons";
import { fetchAllCars } from "../../api/cars";
import { Link } from "react-router-dom";
import example_img from "../../img/example_img.png";
import Carmeter from "../../img/carmeter.svg";

const { Title } = Typography;



function FeatureCarSection(props) {
  const [cars, setCars] = useState([]);
  const [currentForm, setCurrentForm] = useState("signin");
  const [loading, setLoading] = useState();
  const [visible, setVisible] = useState(false);
  const fetchCars = async () => {
    try {
      setLoading(true);
      const res = await fetchAllCars();
      if (res.data && res.data.cars) {
        setCars(res.data.cars);
      }
      setLoading(false);
    } catch (error) {
      setLoading(false);
    }
  };
  const imgStyle = {
    maxHeight: "160px", width: "100%", objectFit: "cover"
  }
  const { dataSource, isMobile, ...tagProps } = props;
  const { blockWrapper, titleWrapper } = dataSource;
  const childrenToRender = cars.slice(0, 8).map((item, index) => {
    return (
      <Col key={index} span={6} style={{ marginBottom: "1rem" }}>

        <Card
          onClick={() => {
            if (!window.localStorage.token) {
              setVisible(true);
            }
          }}
          style={{ padding: "0px" }}
          cover={
            <Link to={window.localStorage.token && `/view-ad/${item._id}`}>
              <Carousel autoplay>
                {item.carImages &&
                  item.carImages.map((item) => {
                    console.log(item);
                    return (
                      <div>
                        <img style={imgStyle}
                          src={`${item}`} alt="" />
                      </div>
                    );
                  })}
              </Carousel>
            </Link>
          }

          loading={loading}
          bodyStyle={{ padding: "10px 24px" }}
          actions={[
            <ContactsOutlined style={{ fontSize: "27px" }} key="setting" />,
            <MessageOutlined style={{ fontSize: "27px" }} key="edit" />,
            <>
              {item.isFavourite ? (
                <HeartFilled
                  onClick={async (event) => {
                    await markFav({
                      carId: item._id,
                      status: false,
                    });
                    fetchCars()
                  }}
                  style={{ color: "red", fontSize: "27px" }}
                  key="ellipsis"
                />
              ) : (
                <HeartOutlined
                  onClick={async (event) => {
                    await markFav({
                      carId: item._id,
                      status: true,
                    });
                    fetchCars()
                  }}
                  style={{ color: "red", fontSize: "27px" }}
                  key="ellipsis"
                />
              )}
            </>,
          ]}
        >

          <Title
            ellipsis={true}
            level={3}
            style={{
              display: "flex",
              justifyContent: "space-between",
              color: "#001529",
              margin: "0px",
              marginTop: "10px",
            }}
          >
            {item.make} {item.model} {item.modelYear}
          </Title>
          <Title ellipsis={true} level={3} style={{ color: "#006004", margin: "0px" }}>
            {" "}
              PKR {item.price}
          </Title>
          <Title ellipsis={true} level={4} style={{ color: "#8F8F8F", margin: "0px" }}>
            {item.city}
          </Title>
          <Row align="bottom" gutter={5}>
            <Col>
              <Title level={4} style={{ color: "#8F8F8F", margin: "0px" }}>
                {item.milage}
              </Title>
            </Col>
            <Col>
              <img src={Carmeter} alt=""></img>
            </Col>
          </Row>
        </Card>

      </Col>
    );
  });

  const mountEffect = () => {
    const fetchCars = async () => {
      try {
        setLoading(true);
        const res = await fetchAllCars();
        if (res.data && res.data.cars) {
          setCars(res.data.cars);
        }
        setLoading(false);
      } catch (error) {
        setLoading(false);
      }
    };
    fetchCars();
  };
  useEffect(mountEffect, []);
  return (
    <div {...tagProps} {...dataSource.wrapper}>
      <div {...dataSource.page}>
        <Divider>
          <h1>Featured Vehicles</h1>
        </Divider>
        <OverPack {...dataSource.OverPack}>
          <QueueAnim
            key="queue"
            type="bottom"
            leaveReverse
            interval={50}
            component={Row}
            {...blockWrapper}
          >
            {childrenToRender}
          </QueueAnim>
        </OverPack>
      </div>
    </div>
  );
}

export default FeatureCarSection;
