import React, { useEffect, useState } from "react";
import "antd/dist/antd.css";
import "./viewAd.css";
import { Image, message, Carousel } from "antd";
import { Card, Row, Button, Col, Divider, Descriptions } from "antd";
import { fetchCar, markFav } from "../../api/cars";
import { useParams } from "react-router";
import { } from "../../api/cars";

import {
  HeartFilled,
  HeartOutlined,
} from "@ant-design/icons";
const gridStyle = {
  width: "25%",
  background: "white",
  textAlign: "center",
};
const buttonStyle = {
  backgroundColor: "#FFEA00",
  borderColor: "#FFEA00",
};

const DemoBox = (props) => (
  <p className={`height-${props.value}`}>{props.children}</p>
);
const imgStyle = {
  maxHeight: "500px", width: "100%", objectFit: "cover"
}

const ViewAd = () => {
  const { Meta } = Card;
  const [loading, setLoading] = useState(false);
  const [carDetail, setCarDetail] = useState({});
  const { id } = useParams();
  // const [loading, setLoading] = useState(false);
  const fetchCarDetail = async () => {
    try {
      setLoading(true);
      const res = await fetchCar({ cardId: id });
      if (res.data && res.data.car) {
        setCarDetail(res.data.car);
      }
      setLoading(false);
    } catch (error) {
      setLoading(false);
      message.error(
        "Error while fetching car detail, reload page may resolve the error!"
      );
    }
  };
  const mountEffect = () => {

    fetchCarDetail();
  };

  useEffect(mountEffect, []);
  return (
    <div>
      <Divider></Divider>
      <Row>
        <Col offset={1} style={{ height: "500px" }} span={12}>
          <Carousel autoplay>
            {carDetail.carImages &&
              carDetail.carImages.map((item) => {
                console.log(item);
                return (
                  <div>
                    <Image style={imgStyle}
                      src={`https://car-bar.herokuapp.com/${item}`}
                    />
                  </div>
                );
              })}
          </Carousel>
        </Col>
        <Col offset={3} style={{ marginTop: "120px" }}>
          <Row>
            <Card
              style={{ width: 280, background: "rgba(128, 128, 128, 0.08)", textAlign: "center" }}
            >
              <Meta
                title="Contact Number"
                description={carDetail.contact}
              />
            </Card>
          </Row>
          <Row style={{ marginTop: "20px", marginLeft: "50px" }}>

            {carDetail.isFavourite ?
              <Button
                onClick={async (event) => {
                  await markFav({
                    carId: carDetail._id,
                    status: false,
                  });
                  fetchCarDetail()
                }}

                type="primary"
                shape="round"
                style={buttonStyle}
                size="large"
                icon={<HeartFilled style={{ color: "red", }} />}
              >
                <text style={{ color: "black" }}>Added to Favorites</text>
              </Button> :
              <Button
                onClick={async (event) => {
                  await markFav({
                    carId: carDetail._id,
                    status: true,
                  });
                  fetchCarDetail()
                }}
                type="primary"
                shape="round"
                style={buttonStyle}
                size="large"
                icon={<HeartOutlined style={{ color: "red", }} />}
              >
                <text style={{ color: "black" }}>Add to Favorites</text>
              </Button>
            }
          </Row>
        </Col>
      </Row>
      <Divider />
      <Row>
        <Col offset={1}>
          <Card
            title="Car Information:"
            style={{ width: 1320, background: "rgba(128, 128, 128, 0.08)" }}
          >
            <Card.Grid style={gridStyle}>
              <h3>Model</h3>
              <i class="fa fa-car"></i>
              <h4>{carDetail.model}</h4>
            </Card.Grid>
            <Card.Grid style={gridStyle}>
              <h3>Year</h3>
              <i class="fa fa-calendar"></i>
              <h4>{carDetail.modelYear}</h4>
            </Card.Grid>
            <Card.Grid style={gridStyle}>
              <h3>Make</h3>
              <i class="fa fa-car"></i>
              <h4>{carDetail.make}</h4>
            </Card.Grid>
            <Card.Grid style={gridStyle}>
              <h3>Mileage</h3>
              <i class="fa fa-dashboard" style={{ fontSize: "16px" }}></i>
              <h4>{carDetail.milage}</h4>
            </Card.Grid>
          </Card>
        </Col>
      </Row>
      <Divider></Divider>
      <Row>
        <Col offset={1}>
          <Descriptions
            title="Car Details"
            column={1}
          >
            <Descriptions.Item label="City">{carDetail.city}</Descriptions.Item>
            <Descriptions.Item label="Price">{carDetail.price}</Descriptions.Item>
            <Descriptions.Item label="Description"> {carDetail.description}</Descriptions.Item>
          </Descriptions>
        </Col>
        {/* <Card
          title="Car Details:"
          style={{ width: 1350, background: "rgba(128, 128, 128, 0.08)" }}
        >
          <Row>
            <Col >
              <h4>
                <i class="fa fa-file-text-o"></i>Description:
              </h4>
            </Col>
            <Col >
              <div class="box">{carDetail.description}</div>
            </Col>
          </Row>
          <Row>
            <Col flex={1.63}>
              <h4>
                <i class="fa fa-address-card"></i>City:
              </h4>
            </Col>
            <Col flex={25}>
              <div class="box"> {carDetail.city}</div>
            </Col>
          </Row>
          <Row>
            <Col flex={1.35}>
              <h4>
                <i class="fa fa-money"></i>Price:
              </h4>
            </Col>
            <Col flex={15}>
              <div class="price_box">{carDetail.price}</div>
            </Col>
          </Row>
        </Card> */}
      </Row>
    </div>
  );
};

export default ViewAd;
