import React, { useState } from "react";
import {

  Typography,
  Col,
  Row,
  Divider,
  Avatar,
  Drawer,
} from "antd";
import Background from "../../img/backgroundCar.jpg";
import profilesvg from "../../img/profile.svg";
import SignInForm from "./SignInForm";
import SignupForm from "./SignupForm";

const { Link } = Typography;


const SingIn = () => {
  const [visible, setVisible] = useState(false);
  const [currentForm, setCurrentForm] = useState("signin");
  const showDrawer = () => {
    setVisible(true);
  };
  const onClose = () => {
    setVisible(false);
  };

  return (
    <>
      <Avatar
        onClick={showDrawer}
        shape="square"
        size={53}
        src={profilesvg}
      ></Avatar>
      <Drawer
        placement="right"
        closable={true}
        onClose={onClose}
        visible={visible}
        width={400}
      >
        <Row justify="center" align="bottom" style={{ marginTop: 50 }}>
          <Col>
            {currentForm === "signin" && <SignInForm />}
            {currentForm === "signup" && <SignupForm />}
            <Divider style={{ marginBottom: 0, paddingBottom: 5 }}></Divider>

            <Row justify="center" gutter={[0, 20]}>
              <Divider type="vertical"></Divider>
            </Row>
            <Row justify="center">
              <Link
                onClick={() => {
                  setCurrentForm(
                    currentForm === "signup" ? "signin" : "signup"
                  );
                }}
              >
                {currentForm === "signup"
                  ? "Already have account?"
                  : "Register Now!"}
              </Link>
            </Row>
          </Col>
        </Row>
        <Row style={{ position: "absolute", bottom: 0, right: 0 }}>
          <Col>
            <img src={Background} alt="" style={{ width: 100 }} />
          </Col>
        </Row>
      </Drawer>   
    </>
  );
};
export default SingIn;
