import React, { useState, useEffect } from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Home from "./Home";
import About from "./pages/About";
import "./App.css";
import "antd/dist/antd.css";
import "./less/App.less";
import Footer from "./components/Footer/Index";
import Nav from "./components/Nav";
import { enquireScreen } from "enquire-js";
import NoMatch from "./pages/NoMatch";
import PostAd from "./pages/postAd/PostAd";
import Cars from "./pages/Cars";
import ViewAd from "./components/ViewAd/ViewAd";
import Login from "./pages/Login";

let isMobile;
enquireScreen((b) => {
  isMobile = b;
});

const { location } = window;
const App = () => {
  const [_isMobile, setIsMobile] = useState(isMobile);
  const [show, setShow] = useState(!location.port);
  useEffect(() => {
    enquireScreen((b) => {
      setIsMobile(!!b);
    });

    if (location.port) {
      setShow(true);
    }
    // }, 500);
  }, []);
  return (
    <Router>
      <Nav id="Nav3_0" key="Nav3_0" isMobile={_isMobile} />
      <Switch>
        <Route path="/about" exact>
          <About />
        </Route>
        <Route path="/" exact>
          <Home />
        </Route>
        <Route path="/login" exact>
          <>
            <Cars />
            <Login />
          </>
        </Route>
        <Route path="/post-ad" exact>
          <PostAd />
        </Route>
        <Route path="/post-ad/:id" exact>
          <PostAd />
        </Route>
        <Route path="/view-ad/:id" exact>
          <ViewAd />
        </Route>
        <Route path="/cars" exact>
          <Cars />
        </Route>
        <Route path="*">
          <NoMatch />
        </Route>
      </Switch>

      <Footer id="Footer1_0" key="Footer1_0" isMobile={_isMobile} />
    </Router>
  );
};

export default App;
