import React, { useState } from "react";
import { Input, Form, Typography, Button, Row, message } from "antd";
import {
  LoginOutlined,
  SmileOutlined,
  UserOutlined,
  LockOutlined,
} from "@ant-design/icons";
import { signUp } from "../../api/auth";

const { Text, Title } = Typography;
const buttonStyle = {
  backgroundColor: "#FFEA00",
  borderColor: "#FFEA00",
};
const fontStyle = {
  fontFamily: "agrandir",
};

const SignupForm = () => {
  const [loading, setLoading] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const handleRegister = async (values) => {
    try {
      setLoading(true);
      const res = await signUp(values);
      if (res.data && res.data.token) {
        window.localStorage.setItem("token", res.data.token);
        message.success("Registered Successfully!");
        window.location.reload();
      }

      setLoading(false);
    } catch (error) {
      setLoading(false);
      if (
        error.response &&
        error.response.data &&
        error.response.data.message
      ) {
        setErrorMsg(
          error.response.data.message || "Something went wrong, try again."
        );
      }
    }
  };
  return (
    <Form onFinish={handleRegister}>
      <Row justify="center">
        <Title style={fontStyle}>Sign Up</Title>
      </Row>
      <Row justify="center">
        <Form.Item
          name="firstName"
          rules={[{ required: true, message: "Please input your First Name" }]}
        >
          <Input prefix={<SmileOutlined />} placeholder="Firt Name" />
        </Form.Item>
      </Row>
      <Row justify="center">
        <Form.Item
          name="lastName"
          rules={[{ required: true, message: "Please input your Last Name" }]}
        >
          <Input prefix={<SmileOutlined />} placeholder="Last Name" />
        </Form.Item>
      </Row>
      <Row justify="center">
        <Form.Item
          name="contactNumber"
          rules={[{ required: true, message: "Please input your Contact" }]}
        >
          <Input prefix={<SmileOutlined />} placeholder="Contact Number" />
        </Form.Item>
      </Row>
      <Row justify="center">
        <Form.Item
          name="email"
          rules={[{ required: true, message: "Please input your Email" }]}
        >
          <Input prefix={<UserOutlined />} placeholder="Email" />
        </Form.Item>
      </Row>
      <Row justify="center">
        <Form.Item
          name="password"
          rules={[{ required: true, message: "Please input your Password!" }]}
        >
          <Input
            prefix={<LockOutlined />}
            type="password"
            placeholder="Password"
          />
        </Form.Item>
      </Row>
      <Row justify="center">
        <Button
          type="primary"
          htmlType="submit"
          shape="round"
          style={buttonStyle}
          loading={loading}
          icon={<LoginOutlined style={{ color: "black" }} />}
          size="large"
        >
          <Text style={{ color: "black" }}>Sign Up</Text>
        </Button>
      </Row>
    </Form>
  );
};

export default SignupForm;
