import axios from "./index";

export const fetchAllCars = async () => {
  return axios.get("/api/cars");
};

export const fetchCar = async ({ cardId }) => {
  return axios.get(`/api/car/${cardId}`);
};

export const addPost = async (payload) => {
  return axios.post(`/api/post/car`, payload);
};

export const updatePost = async ({ id, payload }) => {
  debugger
  return axios.put(`/api/post/car/${id}`, { ...payload });
};

// fetch  user fav cars
export const fetchMyFavCars = async (payload) => {
  return axios.get(`/api/cars/favourite`, payload);
};

// Mark car as fav
export const markFav = async ({ carId, status }) => {
  return axios.post(`/api/mark/fav`, { carId, status });
};

// fetch user's posts
export const fetchMyPosts = async () => {
  return axios.get(`/api/my/posts`);
};
