import React, { useState, useEffect } from "react";
import {
  Drawer,
  Card,
  Typography,
  Avatar,
  Divider,
  Col,
  Row,
  Button,
  message,
  Carousel,
  Empty,
} from "antd";
import { Image } from "antd";
import {
  EditOutlined,
  LogoutOutlined,
  DeleteOutlined,
  HeartOutlined,
  ContactsOutlined,
  MessageOutlined,
  HeartFilled,
} from "@ant-design/icons";

import { me } from "../../api/auth";
import { markFav, fetchMyFavCars, fetchMyPosts } from "../../api/cars";
import { Link } from "react-router-dom";

const { Text, Title } = Typography;

const DescriptionItem = ({ title, content }) => (
  <div className="site-description-item-profile-wrapper">
    <p className="site-description-item-profile-p-label">{title}:</p>
    {content}
  </div>
);
const imgStyle = {
  maxHeight: "160px", width: "100%", objectFit: "cover"
}
const buttonStyle = {
  backgroundColor: "#FFEA00",
  borderColor: "#FFEA00",
};

const Profile = () => {
  const [visible, setVisible] = useState(false);
  const [loading, setLoading] = useState(false);
  const [myCars, setMyCars] = useState([]);
  const [myFavCars, setMyFavCars] = useState([]);
  const [user, setUser] = useState({});
  const showDrawer = () => {
    setVisible(true);
  };

  const onClose = () => {
    setVisible(false);
  };

  const userProfile = async () => {
    try {
      setLoading(true);
      const res = await me();
      console.log(res);
      if (res.data && res.data.user) {
        setUser(res.data.user);
      }
      setLoading(false);
    } catch (error) {
      message.error(
        "Error while fetching user profile, Reload page may fix the error."
      );
      setLoading(false);
    }
  };
  const _fetchMyPosts = async () => {
    try {
      const res = await fetchMyPosts();
      if (res.data && res.data.cars) {
        setMyCars(res.data.cars);
      }
    } catch (error) {
      message.error("Error while loading my posts.");
    }
  };
  const _fetchMyFavCars = async () => {
    try {
      const res = await fetchMyFavCars();
      if (res.data && res.data.cars) {
        setMyFavCars(res.data.cars);
      }
    } catch (error) {
      message.error("Error while loading my fav cars.");
    }
  };
  const mountEffect = () => {
    userProfile();
    _fetchMyFavCars();
    _fetchMyPosts();
  };

  useEffect(mountEffect, [visible]);
  return (
    <>
      <Avatar onClick={showDrawer} src="" />
      <Drawer
        width={550}
        placement="right"
        closable={false}
        onClose={onClose}
        visible={visible}
      >
        <p
          className="site-description-item-profile-p"
          style={{ marginBottom: 24 }}
        >
          <Divider>User Profile</Divider>
        </p>
        <p className="site-description-item-profile-p"></p>
        <Row>
          <Col />
          <Col offset={6}>
            <Avatar size={200} onClick={showDrawer} src="" />
          </Col>
        </Row>
        <Divider></Divider>
        <Row justify="space-between">
          <Col>
            <Text>
              <b>First Name: </b>
              {user.firstName}
            </Text>
          </Col>
          <Col>
            <Text>
              <b>Last Name: </b>
              {user.lastName}
            </Text>
          </Col>
        </Row>
        <Row>
          <Col>
            <Text>
              <b>Email: </b>
              {user.email}
            </Text>
          </Col>
        </Row>
        <Row>
          <Col>
            <Text>
              <b>Phone Number: </b>
              {user.contactNumber}
            </Text>
          </Col>
        </Row>
        <Divider />
        <p className="site-description-item-profile-p">
          <strong>Favorits</strong>
        </p>

        <Row
          style={{
            margin: "1rem",
            padding: "1rem",
          }}
        >
          {myFavCars &&
            myFavCars.map((item, index) => {
              return (
                <Col key={index} span={12} style={{ marginBottom: "0.7rem" }}>

                  <Card
                    onClick={() => {
                      if (!window.localStorage.token) {
                        setVisible(true);
                      }
                    }}
                    style={{ padding: "0px" }}
                    cover={
                      <Link
                        to={window.localStorage.token && `/view-ad/${item._id}`}
                      >
                        <Carousel autoplay>
                          {item.carImages &&
                            item.carImages.map((item) => {
                              console.log(item);
                              return (
                                <div align="center">
                                  <img style={imgStyle}
                                    src={`https://car-bar.herokuapp.com/${item}`}
                                    alt=""
                                  />
                                </div>
                              );
                            })}
                        </Carousel>
                      </Link>
                    }
                    loading={loading}
                    bodyStyle={{ padding: "10px 24px" }}
                    actions={[
                      <>
                        {item.isFavourite ? (
                          <HeartFilled
                            onClick={async () => {
                              await markFav({
                                carId: item._id,
                                status: false,
                              });
                              _fetchMyFavCars()
                            }}
                            style={{ color: "red", fontSize: "27px" }}
                            key="ellipsis"
                          />
                        ) : (
                          <HeartOutlined
                            onClick={async () => {
                              await markFav({
                                carId: item._id,
                                status: true,
                              });
                              _fetchMyFavCars()

                            }}
                            style={{ color: "red", fontSize: "27px" }}
                            key="ellipsis"
                          />
                        )}
                      </>,
                    ]}
                  >
                    <Title
                      ellipsis={true}
                      level={3}
                      style={{
                        display: "flex",
                        justifyContent: "space-between",
                        color: "#001529",
                        margin: "0px",
                        marginTop: "10px",
                      }}
                    >
                      {item.make} {item.model} {item.modelYear}
                    </Title>
                    <Title
                      level={3}
                      style={{ color: "#006004", margin: "0px" }}
                    >
                      {" "}
                        PKR {item.price}
                    </Title>
                  </Card>
                </Col>
              );
            })}
        </Row>
        <Divider />
        <p className="site-description-item-profile-p">
          <strong>Your Ads</strong>
        </p>
        <Row
          style={{
            margin: "1rem",
            padding: "1rem",
          }}
        >
          {myCars.map((item, index) => {
            return (
              <Col key={index} span={12}>
                <Link to={window.localStorage.token && `/view-ad/${item._id}`}>
                  <Card
                    onClick={() => {
                      if (!window.localStorage.token) {
                        setVisible(true);
                      }
                    }}
                    style={{ padding: "0px" }}
                    cover={
                      <Carousel autoplay>
                        {item.carImages &&
                          item.carImages.map((item) => {
                            console.log(item);
                            return (
                              <div align="center">

                                <img style={imgStyle}
                                  src={`https://car-bar.herokuapp.com/${item}`}
                                  alt=""
                                />

                              </div>
                            );
                          })}
                      </Carousel>
                    }
                    loading={loading}
                    bodyStyle={{ padding: "10px 24px" }}
                    actions={[

                      <EditOutlined
                        style={{ fontSize: "27px" }}
                        key="edit"
                      />,
                    ]}
                  >
                    <Title
                      ellipsis={true}
                      level={3}
                      style={{
                        display: "flex",
                        justifyContent: "space-between",
                        color: "#001529",
                        margin: "0px",
                        marginTop: "10px",
                      }}
                    >
                      {item.make} {item.model} {item.modelYear}
                    </Title>
                    <Title
                      ellipsis={true}
                      level={3}
                      style={{ color: "#006004", margin: "0px" }}
                    >
                      {" "}
                      PKR {item.price}
                    </Title>
                  </Card>
                </Link>
              </Col>
            );
          })}{" "}
        </Row>
        <Row justify="space-between" style={{ marginTop: "30px" }}>
          <Col>
            <Button
              type="primary"
              shape="round"
              style={buttonStyle}
              icon={<EditOutlined style={{ color: "black" }} />}
              size="large"
            >
              <Text color="Black">Edit Profile</Text>
            </Button>
          </Col>
          <Col>
            <Button
              type="primary"
              shape="round"
              onClick={() => {
                window.localStorage.removeItem("token");
                window.location.reload();
              }}
              style={buttonStyle}
              icon={<LogoutOutlined style={{ color: "black" }} />}
              size="large"
            >
              <Text color="Black">Sign Out</Text>
            </Button>
          </Col>
        </Row>
      </Drawer>
    </>
  );
};
export default Profile;
