import React from "react";
import "antd/dist/antd.css";
import "./postAd.css";
import { Row, Col, Divider, message, Card } from "antd";
import { Input } from "antd";
import { Upload, Button } from "antd";
import { UploadOutlined } from "@ant-design/icons";
import iconPost from "./send-button.svg";
import iconEngine from "./car-engine.svg";
import iconAd from "./ad.svg";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import { addPost, fetchCar, updatePost } from "../../api/cars";
import PicturesWall from "../../components/PicturesWall";
import { useHistory, useParams } from "react-router";
export const FILE_SIZE = 1600 * 1024;
export const SUPPORTED_FORMATS = [
  "image/jpg",
  "image/jpeg",
  "image/gif",
  "image/png",
];

const FormikInput = ({
  field: { name, type, ...rest },
  className,
  errors,
  form,
  ...props
}) => {
  const error = errors[name];

  return (
    <span className={className}>
      <Input name={name} {...rest} {...props} />
      <div style={{ color: "red", width: "25%" }}>{error}</div>
    </span>
  );
};

const PostAd = () => {
  const buttonStyle = {
    backgroundColor: "#FFEA00",
    borderColor: "#FFEA00",
  };
  const [carDetail, setCarDetail] = React.useState({});
  const { id } = useParams();
  // const [initialValues, setInitialValues] = React.useState();
  const fileList = [];
  const [loading, setLoading] = React.useState(false);
  const history = useHistory()

  const schema = Yup.object().shape({
    model: Yup.string().required("Required"),
    modelYear: Yup.string().required("Required"),
    make: Yup.string().required("Required"),
    description: Yup.string().required("Required"),
    price: Yup.string().required("Required"),
    engine: Yup.string().required("Required"),
    milage: Yup.string().required("Required"),
    city: Yup.string().required("Required"),
    registeredCity: Yup.string().required("Required"),
    color: Yup.string().required("Required"),
    // carImages: Yup.array()
    //   .of(
    //     Yup.object().shape({
    //       file: Yup.mixed()
    //         .optional()
    //         .test("fileSize", "File too large", (value) => {
    //           if (!value) {
    //             return true;
    //           }
    //           if (value && value.size <= FILE_SIZE) {
    //             return true;
    //           }
    //         })
    //         .test("fileFormat", "Unsupported Format", (value) => {
    //           if (!value) {
    //             return true;
    //           }
    //           if (value && SUPPORTED_FORMATS.includes(value.type)) {
    //             return true;
    //           }
    //         }),
    //     })
    //   )
    //   .optional(),
  });

  // const [loading, setLoading] = useState(false);
  const mountEffect = () => {
    const fetchCarDetail = async () => {
      try {
        if (id) {
          setLoading(true);
          const res = await fetchCar({ cardId: id });
          if (res.data && res.data.car) {
            setCarDetail(res.data.car);
            // const {
            //   model,
            //   modelYear,
            //   make,
            //   description,
            //   price,
            //   engine,
            //   milage,
            //   city,
            //   registeredCity,
            //   color,
            // } = res.data.car;
            // setInitialValues({
            //   model,
            //   modelYear,
            //   make,
            //   description,
            //   price,
            //   engine,
            //   milage,
            //   city,
            //   registeredCity,
            //   color,
            // });
          }
          setLoading(false);

        }
      } catch (error) {
        setLoading(false);
        message.error(
          "Error while fetching car detail, reload page may resolve the error!"
        );
      }
    };

    fetchCarDetail();
  };

  React.useEffect(mountEffect, []);

  return (

    <Row justify="center">
      <Col span={12}>
        {!loading && (
          <Formik
            initialValues={
              {
                model: "",
                modelYear: "",
                make: "",
                description: "",
                price: "",
                engine: "",
                milage: "",
                city: "",
                registeredCity: "",
                color: "",
                carImages: [],
              }
            }
            validationSchema={schema}
            onSubmit={async (values, { setSubmitting }) => {
              try {
                setLoading(true);
                const data = new FormData();
                for (const key in values) {
                  if (Object.hasOwnProperty.call(values, key)) {
                    const value = values[key];
                    if (key !== "carImages") {
                      data.append(key, value);
                    }
                  }
                }
                values.carImages.forEach((item) => {
                  data.append("carImages", item.file);
                });

                await addPost(data);
                history.push("/")
                // if (id) {
                //   await updatePost({ id, payload: data });
                // } else {
                // }
                setLoading(false);
                message.success("Added successfully!");
              } catch (error) {
                console.log(error);
                setLoading(false);
                message.error("Error while posting ad.");
              }
            }}
          >
            {({ isSubmitting, errors, setFieldValue }) => {
              console.log(errors);
              return (
                <Form>
                  <Card
                    style={{ marginTop: 16 }}
                    type="inner"
                    title="Car Info"
                  >
                    <PicturesWall setFieldValue={setFieldValue} />
                    {/* <input type="file" name="" id="" onChange={(event)=>{
                    this.se
                  }}/> */}
                    <br /> <label htmlFor="">Model</label>
                    <Field
                      component={FormikInput}
                      errors={errors}
                      name="model"
                      placeholder="Model"
                    />
                    <br /> <label htmlFor="">Make</label>
                    <Field
                      component={FormikInput}
                      errors={errors}
                      name="make"
                      placeholder="Make"
                    />
                    <br /> <label htmlFor="">City</label>
                    <Field
                      component={FormikInput}
                      errors={errors}
                      name="city"
                      placeholder="City"
                    />
                    <br /> <label htmlFor="">Registration City</label>
                    <Field
                      component={FormikInput}
                      errors={errors}
                      name="registeredCity"
                      placeholder="Registration City"
                    />
                    <br /> <label htmlFor="">Model Year</label>
                    <Field
                      component={FormikInput}
                      errors={errors}
                      name="modelYear"
                      placeholder="Model Year"
                    />
                    <br /> <label htmlFor="">Color</label>
                    <Field
                      component={FormikInput}
                      errors={errors}
                      name="color"
                      placeholder="Col"
                    />
                    <br /> <label htmlFor="">Mileage (km)</label>
                    <Field
                      component={FormikInput}
                      errors={errors}
                      name="milage"
                      placeholder="Mileage (km)"
                    />
                    <br /> <label htmlFor="">Price</label>
                    <Field
                      component={FormikInput}
                      errors={errors}
                      name="price"
                      placeholder="Price"
                    />
                    <br /> <label htmlFor="">Contact</label>
                    <Field
                      component={FormikInput}
                      errors={errors}
                      name="contact"
                      placeholder="Contact"
                    />
                    <br /> <label htmlFor="">Enigne</label>
                    <Field
                      component={FormikInput}
                      errors={errors}
                      name="engine"
                      placeholder="Engine"
                    />
                    <br /> <label htmlFor="">Description</label>
                    <Field
                      component={FormikInput}
                      errors={errors}
                      name="description"
                      placeholder="Description"
                    />
                    <br /> <label htmlFor="">Address</label>
                    <Field
                      component={FormikInput}
                      errors={errors}
                      name="address"
                      placeholder="Address"
                    />
                    <br />
                    <Row>
                      <Col>
                        <Button
                          type="primary"
                          loading={loading}
                          htmlType="submit"
                          style={buttonStyle}
                        >

                          <img src={iconPost} />
                          <text style={{ color: "black" }}>Post</text>
                        </Button>
                      </Col>
                    </Row>
                  </Card>
                </Form>
              );
            }}
          </Formik>
        )}
      </Col>
    </Row>
  );
};

export default PostAd;
