import React from "react";
import { enquireScreen } from "enquire-js";

import Filter from "./components/Filter";
import Banner from "./components/Banner";
import Brands from "./components/BrandSection/index";
import FeaturedCars from "./components/FeaturedCars";
import PopularCars from "./components/PopularCars";
import More from "./components/More";

let isMobile;
enquireScreen((b) => {
  isMobile = b;
});

const { location } = window;

export default class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isMobile,
      show: !location.port,
    };
  }

  componentDidMount() {
    enquireScreen((b) => {
      this.setState({ isMobile: !!b });
    });
    if (location.port) {
      this.setState({
        show: true,
      });
    }
  }

  render() {
    const children = [
      <Banner id="Banner1_0" key="Banner1_0" isMobile={this.state.isMobile} />,
      <Filter />,
      <FeaturedCars
        id="Feature7_0"
        key="Feature7_0"
        isMobile={this.state.isMobile}
      />,
      <More />,
      <Brands
        id="Content5_0"
        key="Content5_0"
        isMobile={this.state.isMobile}
      />,
      <PopularCars
        id="Feature7_0"
        key="Feature7_0"
        isMobile={this.state.isMobile}
      />,
    ];
    return (
      <div
        className="templates-wrapper"
        ref={(d) => {
          this.dom = d;
        }}
      >
        {this.state.show && children}
      </div>
    );
  }
}
